.login-form {
    max-width: 300px;
    margin: auto auto;
  }
.login-form-forgot {
    float: right;
  }
.login-form-button {
    width: 100%;
}
.register-form {
    max-width: 800px;
    margin: auto auto;
  }
.article-info-div{
    margin-top: 10px;
    margin-bottom: 15px;
}
.articlesingle {
    border-radius:2px;
    box-shadow: 0px 0px 10px #a3a2a2;
    background: rgb(255, 255, 255); 
    padding:0px 5px 5px 5px; 
}
.article-title{
    height:30%;
    font-size: 18px;
    font-weight: 700;
    line-height: 1.5;
}
.article-breif{
    height:50%;
    text-align: justify;
    text-justify:inter-ideograph;
}
.article-note{
    height:20%;
}
.article-url-link{
    text-decoration:none;
    color: black;
}
.article-url-link:hover{
    text-decoration:none;
    color: black;
}
.article-url-link:active{
    text-decoration:none;
    color: black;
}
.article-url-link:link{
    text-decoration:none;
    color: black;
}
.articleCover{
    margin: 5% auto;
    width:100%;
    height:90%;
}

.commentscomponent{
    margin: 0 5px 0 5px;
    border:1px solid rgb(235,235,235);
    box-shadow: 0px 0px 10px #a3a2a2;
    background: rgb(255, 255, 255); 
    padding-bottom: 5px;
}
.commentsTitle{
    padding-left: 10px;
    font-size: 18px;
    font-weight: 700;
}
.singlecomment{
    margin:0 0 0 -30px;
}
.commetnHline{
    color: rgb(242, 247, 247);
    opacity:0.5;
}
.commentTime{
    padding-right: 20px;
}
.commentBody{
    padding-left: 5px;
    margin:3px auto;
}
.commentFoot{
    cursor:pointer;
}

.articlecom{
    width:800px;;
    margin: 0 auto;
}




body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(246,246,246);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

